  margin-bottom: 20px;
}

.cover-mask {
  position: absolute;
  top: 0;
  left: 15px;
  width: 80px;
  height: 80px;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 3px;
  line-height: 80px;
  text-align: center;
  color: #fff;
  cursor: pointer;
}

.thumbnail-mask .cover-mask {
  display: none;
}

.thumbnail-mask:hover .cover-mask,
.thumbnail-mask:hover .cover-delete {
  display: block;
}

.cover-label {
  position: absolute;
  top: 0;
  left: 15px;
  background: #00cc3d;
  color: #fff;
  padding: 2px 5px;
  font-size: 12px;
}

.cover-delete {
  display: none;
  position: absolute;
  top: -8px;
  right: -9px;
  background: #f00;
  color: #fff;
  font-size: 10px;
  border-radius: 30px;
  width: 18px;
  height: 18px;
  text-align: center;
  line-height: 20px;
  cursor: pointer;
}

.list-group-item > a {
  float: right;
}



