  margin-bottom: 20px;
}

.cover-mask {
  position: absolute;
  top: 0;
  left: 15px;
  width: 80px;
  height: 80px;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 3px;
  line-height: 80px;
  text-align: center;
  color: #fff;
  cursor: pointer;
}

.thumbnail-mask .cover-mask {
  display: none;
}

.thumbnail-mask:hover .cover-mask {
  display: block;
}

.cover-label {
  position: absolute;
  top: 0;
  left: 15px;
  background: #00cc3d;
  color: #fff;
  padding: 2px 5px;
  font-size: 12px;
}

.w120{
  width: 120px;
}
.w245{
  width: 252px;
}

.tab-content{
  background: white;
  margin: 0px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}



