





































































.modal-content {
  box-shadow: none;
}

.upload-modal {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9999999;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.modal-body {
  overflow-y: scroll;
}

.thumbnail-mask {
  cursor: pointer;
  position: relative;
}

.wx-list-cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  background-image: url(http://weazm-cdn.qiniudn.com/selected30.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
}



