







































































































































.mall-order-detail li {
  list-style: none;
  margin: 10px auto;
}

#progressbar {
  overflow: hidden;
  /*CSS counters to number the steps*/
  counter-reset: step;
  padding: 40px 0;
}

#progressbar li {
  list-style-type: none;
  color: #999396;
  text-align: center;
  text-transform: uppercase;
  font-size: 16px;
  width: 25%;
  float: left;
  position: relative;
}

#progressbar li:before {
  content: counter(step);
  counter-increment: step;
  width: 30px;
  height: 30px;
  display: block;
  font-size: 10px;
  color: #fff;
  background: #ddd;
  border-radius: 15px;
  margin: 0 auto 5px auto;
  border: 2px solid #fff;
  position: relative;
  z-index: 2;
  font-size: 14px;
  font-weight: bold;
  line-height: 25px;
  color: #fff;
}

/*progressbar connectors*/

#progressbar li:after {
  content: '';
  width: 100%;
  height: 5px;
  background: #ddd;
  position: absolute;
  left: -50%;
  top: 12px;
  z-index: 1;
  /*put it behind the numbers*/
}

#progressbar li:first-child:after {
  content: none;
}

#progressbar li.active:before,
#progressbar li.active:after {
  background: #31bf56;
  color: white;
}



